body, html,
h1, h2, h3,
h4, h5, h6,
ul, ol, li,
dl, dt, dd,
p, hr {
  margin: 0;
  padding: 0;
}

ul, ol {
  list-style: none;
}

body,
html {
  font-family: Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  font-size: 100px;
}

a {
  text-decoration: none;
}

a:hover, a:active, a:link, a:visited {
  text-decoration: none;
}

dt {
  font-weight: normal;
}

.container {
  font-size: 0;
}

.headline {
  margin: 0;
  padding: .2rem 0 .1rem;
  text-align: center;
  color: #333;
  font-size: .5rem;
}

.main li {
  list-style: decimal inside;
  font-size: .2rem;
  padding-top: .1rem;
}

@media print {
  .main li {
    font-size: .26rem;
  }
}

.main li.z-right:after, .main li.z-error:after {
  content: '';
  display: inline-block;
  width: .2rem;
  height: .2rem;
}

.main li.z-right:after {
  background: url("../img/icon.png") right center/auto 100% no-repeat;
}

.main li.z-error:after {
  background: url("../img/icon.png") left center/auto 100% no-repeat;
}

.main li span,
.main li input {
  display: inline-block;
  font-weight: bold;
}

.main li input {
  width: .5rem;
  padding-left: .05rem;
  border: none;
  line-height: 1;
}

.main li input:focus {
  outline: 0;
}

@media print {
  .main li input {
    display: none;
  }
}

.main li span:nth-child(2) {
  padding: 0 .04rem;
}

.time,
.mistake,
.computation {
  font-size: .2rem;
}

@media print {
  .time,
  .mistake,
  .computation {
    display: none;
  }
}

.time big,
.mistake big,
.computation big {
  text-transform: capitalize;
  font-weight: bold;
}

.time span,
.mistake span,
.computation span {
  font-size: .3rem;
  font-weight: bold;
  color: #dc3545;
}

.time span:nth-child(2), .time span:nth-child(4) {
  font-size: .25rem;
}

.computation span {
  display: inline-block;
  margin-top: 0;
  padding-right: .04rem;
  padding-left: .04rem;
  text-transform: capitalize;
  font-size: .2rem;
  color: #333;
  cursor: pointer;
}

.computation span:hover, .computation span.z-current {
  color: #dc3545;
}

.computation span.z-current {
  text-decoration: underline;
}

.btn {
  margin-top: .1rem;
  padding: .02rem .3rem;
  font-size: .2rem;
}

@media print {
  .btn {
    display: none;
  }
}

.btn.btn-info {
  margin-bottom: .3rem;
}

.btn-info:focus, .btn-info:not(:disabled):not(.disabled):active:focus {
  box-shadow: none;
}
